import * as actionTypes from './actionTypes';

export const initialState = {
  persons: {},
};

export const persons = (state = initialState.persons, action = {}) => {
  switch (action.type) {
    case actionTypes.GET_PERSON_SUCCESS: {
      const { result } = action.payload;
      if (!state[result.id]) {
        state[result.id] = { ...result };
      }
      return { ...state };
    }
    case actionTypes.GET_PERSON_START:
    case actionTypes.GET_PERSON_FAIL:
    default:
      return state;
  }
};

export const getMovies = (state = initialState.movies) => state;
export const getPersons = (state = initialState.persons) => state;

export default combineReducers({
  movies,
  persons,
});
